@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400,500,700');
html,
body {
  margin: 0;
  min-height: 100vh;
  overflow-x: hidden;
}
.consoles .cross,
.consoles .info {
  position: absolute;
  width: 30px;
  height: 30px;
  cursor: pointer;
  margin-left: -2px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto Mono', monospace;
  font-size: 23px;
  font-weight: bold;
  color: #06086e;
}
.consoles .info {
  background-color: #cbff00;
}
.consoles .cross {
  background-color: #fff;
  margin-top: -32px;
}
.consoles .img-console {
  border: solid 2px #fff;
  display: inline-block;
  position: absolute;
}
header.hors_format,
footer.hors_format {
  z-index: 100;
}
.pxl-grand-format {
  position: relative;
  min-height: 100%;
  margin: 0;
  font-size: 16px;
  line-height: 1.4;
  box-sizing: border-box;
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
  overflow: hidden;
  background: linear-gradient(180deg, #12346d, #06b586);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.pxl-grand-format *,
.pxl-grand-format *:after,
.pxl-grand-format *:before {
  box-sizing: inherit;
}
.pxl-grand-format a {
  color: #000;
  text-decoration: none;
  font-style: italic;
}
.pxl-grand-format a a:hover,
.pxl-grand-format a a:focus {
  color: #000;
}
.pxl-grand-format figure img,
.pxl-grand-format article img {
  border: none;
}
.pxl-grand-format h1,
.pxl-grand-format h2,
.pxl-grand-format h3,
.pxl-grand-format p {
  margin-top: 1em;
  margin-bottom: 1em;
}
.pxl-grand-format img {
  max-width: 100%;
}
.pxl-grand-format .title-top {
  position: relative;
  max-width: 720px;
  margin-top: 206px;
  margin-left: 10px;
  margin-right: 10px;
}
.pxl-grand-format .title-top h1 {
  font-size: 64px;
  font-weight: 500;
  line-height: 1.08;
  letter-spacing: 6.3px;
  text-align: left;
  color: #f8e81c;
  margin: 0;
}
.pxl-grand-format .title-top p {
  display: inline-block;
  font-size: 23px;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.3px;
  text-align: left;
  margin: 0.5em;
}
.pxl-grand-format .title-top span {
  color: #06086e;
  background-color: #f8e81c;
  box-shadow: 0.5em 0 0 #f8e81c, -0.5em 0 0 #f8e81c;
}
.pxl-grand-format .what-gift {
  position: relative;
  margin-top: 133px;
}
.pxl-grand-format h2 {
  display: inline-block;
  position: relative;
  font-size: 40px;
  font-weight: bold;
  line-height: 1;
  letter-spacing: 3.9px;
  margin: 0 26px 15px 0;
  color: #06086e;
}
.pxl-grand-format h2 div {
  position: relative;
  padding: 6px 16px;
  z-index: 1;
  background: #f8e81c;
}
.pxl-grand-format h2:after {
  content: " ";
  position: absolute;
  top: 26px;
  left: 26px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
}
.pxl-grand-format h2 div:before,
.pxl-grand-format h2 div:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.pxl-grand-format h2 div:before {
  bottom: -27px;
  left: 27px;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-left: 19px solid #c40f0f;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.pxl-grand-format h2 div:after {
  right: 0;
  top: 0;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-right: 19px solid #3230e4;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.pxl-grand-format .buttons-conteneur {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: right;
}
.pxl-grand-format button {
  display: inline-block;
  position: relative;
  color: #06086e;
  font-size: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  margin-right: 26px;
}
.pxl-grand-format button span {
  font-family: 'Roboto Mono', monospace;
  position: relative;
  display: block;
  text-align: right;
  padding: 5px 20px 5px 150px;
  z-index: 1;
  background: #f8e81c;
  font-weight: bold;
  letter-spacing: 2px;
}
.pxl-grand-format button span:before,
.pxl-grand-format button span:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.pxl-grand-format button span:before {
  bottom: -27px;
  left: 27px;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-left: 19px solid #c40f0f;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.pxl-grand-format button span:after {
  right: 0;
  top: 0;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-right: 19px solid #3230e4;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.pxl-grand-format button:after {
  content: " ";
  position: absolute;
  top: 26px;
  left: 26px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
}
.pxl-grand-format .conteneur {
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease;
}
.pxl-grand-format .result {
  position: absolute;
  padding: 120px 20px;
  transition: all 0.5s 0.25s ease;
  transform: translate(0, 100vh);
  background-color: #06b586;
  background-image: linear-gradient(180deg, #12346d, #06b586);
}
.pxl-grand-format.show-result .conteneur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transform: scale(0.8);
  z-index: 0;
}
.pxl-grand-format.show-result .result {
  position: relative;
  transform: translate(0, 0vh);
  z-index: 1000;
}
.what-gift-title {
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 30px;
  z-index: 2;
}
.what-gift-form {
  position: relative;
  display: block;
}
.what-gift-form .cases {
  position: relative;
  z-index: 1;
  display: inline-flex;
  flex-wrap: wrap;
}
.what-gift-form .case {
  width: 221.9px;
  position: relative;
  margin: 10px;
  cursor: pointer;
}
.what-gift-form .case p {
  display: inline-block;
  position: relative;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.67;
  width: 221.9px;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1.8px;
  text-align: left;
  color: #ffffff;
}
.what-gift-form .case p span {
  display: block;
  position: relative;
  padding: 10px 20px;
  z-index: 1;
  width: 221.9px;
  background: #12346d;
}
.what-gift-form .case p:after {
  content: " ";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, #12346d, #06b586);
}
.what-gift-form .case p span:before,
.what-gift-form .case p span:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.what-gift-form .case p span:before {
  bottom: -10px;
  left: 10px;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid #06b586;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.what-gift-form .case p span:after {
  right: 0;
  top: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 7px solid #12346d;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.what-gift-form .case .check {
  height: 43px;
  width: 43px;
  margin-top: 10px;
  margin-bottom: 10px;
  z-index: 1;
  cursor: pointer;
  left: 20px;
  border: solid 9px #fff;
}
.what-gift-form .case:hover .check,
.what-gift-form .case.active .check {
  background-color: #fff;
}
.what-gift-form .case.active p span {
  background-color: #c40f0f;
}
.what-gift-form .case.active p:after {
  background: #fff;
}
.what-gift-form .case.active p span:before,
.what-gift-form .case.active p span:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.what-gift-form .case.active p span:before {
  border-left-color: #fff;
}
.what-gift-form .case.active p span:after {
  border-right-color: #fff;
}
.what-gift-form .suivant {
  position: absolute;
  bottom: 40px;
  left: -100px;
}
.what-gift-form .suivant p {
  display: inline-block;
  position: relative;
  font-size: 40px;
  width: 1175px;
  height: 189px;
  font-weight: bold;
  line-height: 1;
  letter-spacing: 3.9px;
  color: #06086e;
}
.what-gift-form .suivant p span {
  position: relative;
  display: block;
  text-align: right;
  padding-top: 130px;
  padding-right: 15px;
  z-index: 1;
  width: 1175px;
  height: 189px;
  background: #f8e81c;
}
.what-gift-form .suivant p:after {
  content: " ";
  position: absolute;
  top: 26px;
  left: 26px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
}
.what-gift-form .suivant p span:before,
.what-gift-form .suivant p span:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.what-gift-form .suivant p span:before {
  bottom: -27px;
  left: 27px;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-left: 19px solid #c40f0f;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.what-gift-form .suivant p span:after {
  right: 0;
  top: 0;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-right: 19px solid #3230e4;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
/* FINAL */
.final-left,
.final-right {
  position: relative;
  max-width: 1260px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 50px auto 120px auto;
  margin-right: auto;
  overflow: hidden;
}
.final-left a,
.final-right a,
.final-left button,
.final-right button {
  font-style: normal;
  display: inline-block;
  position: relative;
  color: #06086e;
  font-size: 18px;
  font-weight: bold;
  padding: 0;
  border: none;
  background-color: transparent;
  margin: 0px 15px;
}
.final-left a span,
.final-right a span,
.final-left button span,
.final-right button span {
  position: relative;
  display: block;
  text-align: right;
  padding: 15px 35px;
  z-index: 1;
  background: #f8e81c;
}
.final-left a span:before,
.final-right a span:before,
.final-left button span:before,
.final-right button span:before,
.final-left a span:after,
.final-right a span:after,
.final-left button span:after,
.final-right button span:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.final-left a span:before,
.final-right a span:before,
.final-left button span:before,
.final-right button span:before {
  bottom: -10px;
  left: 10px;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid #3230e4;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.final-left a span:after,
.final-right a span:after,
.final-left button span:after,
.final-right button span:after {
  right: 0;
  top: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 7px solid #c40f0f;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.final-left a:after,
.final-right a:after,
.final-left button:after,
.final-right button:after {
  content: " ";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, #c40f0f, #3230e4);
}
.final-left h2,
.final-right h2 {
  display: inline-block;
  position: relative;
  font-size: 40px;
  font-weight: bold;
  line-height: 1;
  letter-spacing: 3.9px;
  color: #fff;
  margin-bottom: 70px;
}
.final-left h2 div,
.final-right h2 div {
  position: relative;
  padding: 10px 16px 10px 70px;
  z-index: 2;
  background: #13346d;
}
.final-left h2 .number,
.final-right h2 .number {
  position: absolute;
  display: block;
  padding: 10px 16px;
  letter-spacing: 0;
  top: 0;
  left: 0;
}
.final-left h2:after,
.final-right h2:after {
  content: " ";
  position: absolute;
  top: 26px;
  left: 26px;
  width: 100%;
  z-index: 1;
  height: 100%;
  background-image: linear-gradient(102deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
}
.final-left h2 div:before,
.final-right h2 div:before,
.final-left h2 div:after,
.final-right h2 div:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
}
.final-left h2 div:before,
.final-right h2 div:before {
  bottom: -27px;
  left: 27px;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-left: 19px solid #c40f0f;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.final-left h2 div:after,
.final-right h2 div:after {
  right: 0;
  top: 0;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-right: 19px solid #3230e4;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.final-left .text-result,
.final-right .text-result {
  position: relative;
  background-color: #fff;
  display: inline-block;
  width: 60%;
  padding: 50px;
  padding-top: 100px;
  margin-top: -100px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.67;
  letter-spacing: 1px;
  text-align: left;
  margin-bottom: 20px;
  color: #13346d;
}
.final-left .img-console-result,
.final-right .img-console-result {
  position: relative;
  max-width: 50%;
  display: inline-block;
  z-index: 1;
  margin-bottom: 26px;
  margin-left: 15%;
}
.final-left .img-console-result img,
.final-right .img-console-result img {
  display: block;
  width: 100%;
  position: relative;
  border: solid 2px #f8e81c;
  z-index: 1;
}
.final-left .img-console-result:before,
.final-right .img-console-result:before,
.final-left .img-console-result:after,
.final-right .img-console-result:after,
.final-left .img-console-result div:before,
.final-right .img-console-result div:before,
.final-left .img-console-result div:after,
.final-right .img-console-result div:after {
  content: " ";
  display: block;
  position: absolute;
}
.final-left .img-console-result div:before,
.final-right .img-console-result div:before {
  width: 27px;
  height: calc(100% - 27px);
  bottom: 0;
  right: -27px;
  background-image: linear-gradient(to bottom, #12346d, #06b586);
}
.final-left .img-console-result div:after,
.final-right .img-console-result div:after {
  bottom: -27px;
  left: 27px;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-left: 19px solid #06b586;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.final-left .img-console-result:before,
.final-right .img-console-result:before {
  width: 100%;
  left: 27px;
  height: 27px;
  bottom: -27px;
  background-color: #06b586;
}
.final-left .img-console-result:after,
.final-right .img-console-result:after {
  top: 0;
  right: 0;
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  border-right: 19px solid #12346d;
  transform: rotate(-45deg);
  transform-origin: 100% 0;
}
.final-left .prix,
.final-right .prix {
  display: inline-block;
  float: right;
  color: #3230e4;
}
.final-left h3,
.final-right h3 {
  margin-bottom: 5px;
}
.final-left h4,
.final-right h4 {
  text-align: right;
  font-size: 16px;
  margin: 0;
}
.final-left .img-console-result {
  float: left;
}
.final-left .text-result {
  float: right;
}
.final-left .buttons {
  position: absolute;
  left: 0;
}
.final-left .buttons button:first-child,
.final-left .buttons a:first-child {
  margin-left: -35px;
}
.final-right .img-console-result {
  margin-left: 0;
  margin-right: 15%;
}
.final-right div {
  text-align: right;
  margin-right: 0;
}
.final-right .h2 {
  text-align: right;
  margin-right: 61px;
}
.final-right .img-console-result {
  float: right;
}
.final-right .text-result {
  float: left;
  margin-right: 0;
}
.final-right .buttons {
  position: absolute;
  right: 0;
}
.final-right .buttons:last-child {
  margin-right: -35px;
}
.who-footer {
  position: relative;
  padding: 120px 10px;
  text-align: center;
}
.table {
  display: table;
}
.row {
  display: table-row;
}
.cell {
  display: table-cell;
  vertical-align: middle;
}
.centerer {
  display: table;
  width: 100%;
  height: 100%;
}
.centered {
  display: table-cell;
  vertical-align: middle;
}
.pxl-center {
  position: relative;
  margin: 0 auto;
  max-width: 1000px;
  padding-left: 15px;
  padding-right: 15px;
}
.text-center {
  text-align: center;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
@media screen and (max-width: 1600px) {
  .consoles .img-console {
    max-width: 400px;
  }
  .consoles .img-console img {
    max-height: 400px;
  }
}
@media screen and (max-width: 1400px) {
  .consoles .img-console {
    max-width: 300px;
  }
  .consoles .img-console img {
    max-height: 300px;
  }
}
@media screen and (max-width: 1024px) {
  .consoles .img-console {
    max-width: 200px;
  }
  .consoles .img-console img {
    max-height: 200px;
  }
  .final-left .text-result,
  .final-right .text-result {
    width: 75%;
  }
  .final-left .img-console-result,
  .final-right .img-console-result {
    max-width: 75%;
  }
}
@media screen and (max-width: 768px) {
  .pxl-grand-format h2 {
    font-size: 24px;
    letter-spacing: 0;
    margin-right: 0;
    margin-bottom: 10px;
  }
  .pxl-grand-format h2 div {
    padding: 6px 10px;
  }
  .pxl-grand-format h2 div:before,
  .pxl-grand-format h2 div:after {
    border-top-width: 12px;
    border-bottom-width: 12px;
  }
  .pxl-grand-format h2 div:before {
    bottom: -17px;
    left: 17px;
    border-left-width: 12px;
  }
  .pxl-grand-format h2 div:after {
    border-right-width: 12px;
  }
  .pxl-grand-format h2 .number {
    padding: 6px 10px;
  }
  .pxl-grand-format h2:after {
    top: 16px;
    left: 16px;
  }
  .pxl-grand-format button {
    font-size: 20px;
  }
  .pxl-grand-format button span {
    padding: 5px 20px 5px 50px;
  }
  .pxl-grand-format .title-top {
    margin-top: 100px;
  }
  .pxl-grand-format .title-top h1 {
    font-size: 36px;
    letter-spacing: 3px;
  }
  .pxl-grand-format .title-top p {
    line-height: 1.8;
    font-size: 16px;
  }
  .pxl-grand-format .pxl-center {
    padding-left: 10px;
    padding-right: 10px;
  }
  .pxl-grand-format .what-gift {
    margin-top: 60px;
    margin-bottom: 120px;
  }
  .what-gift-form {
    margin-top: 60px;
  }
  .what-gift-form .cases {
    margin-left: 5px;
    margin-right: 5px;
    justify-content: center;
  }
  .what-gift-form .case {
    width: 135px;
    margin: 5px;
  }
  .what-gift-form .case p {
    font-size: 14px;
    width: 135px;
    margin: 0;
    letter-spacing: 0;
  }
  .what-gift-form .case p span {
    padding: 5px 10px;
    width: 135px;
  }
  .what-gift-form .case p span:before,
  .what-gift-form .case p span:after {
    border-top-width: 4px;
    border-bottom-width: 4px;
  }
  .what-gift-form .case p span:before {
    bottom: -6px;
    left: 6px;
