@white: #fff;
@fond: #f8f8f8;
@gris: #403f3f;

@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400,500,700');

html, body{
    margin: 0;
    min-height: 100vh;
    overflow-x: hidden;

}
.consoles{

    .cross, .info{
        position: absolute;
        width: 30px;
        height: 30px;
        cursor: pointer;
        margin-left: -2px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Roboto Mono', monospace;
        font-size: 23px;
        font-weight: bold;
        color: #06086e;
    }

    .info{
        background-color: #cbff00;

    }
    .cross{
        background-color: #fff;
        margin-top: -32px;

    }


    .img-console{
        border: solid 2px #fff;
        display: inline-block;
        position: absolute;
    }

}

header.hors_format, footer.hors_format {
    z-index: 100;
}

.pxl-grand-format{

    position: relative;
    min-height: 100%;
    margin: 0;
    font-size: 16px;
    line-height: 1.4;
    box-sizing: border-box;
    font-family: 'Roboto Mono', monospace;
    font-weight: 400;
    overflow: hidden;
    background: linear-gradient(180deg, #12346d, #06b586);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    *, *:after, *:before{
        box-sizing: inherit;
    }

    a{
        color: #000;
        text-decoration: none;
        font-style: italic;
        a{
            &:hover, &:focus{
                color: #000;
            }
        }
    }

    figure img, article img{
        border: none;
    }

    h1, h2, h3, p{
        margin-top: 1em;
        margin-bottom: 1em;
    }

    img{
        max-width: 100%;
    }
    .title-top{
        position: relative;
        max-width: 720px;
        margin-top: 206px;
        margin-left: 10px;
        margin-right: 10px;
        h1{
            font-size: 64px;
            font-weight: 500;
            line-height: 1.08;
            letter-spacing: 6.3px;
            text-align: left;
            color: #f8e81c;
            margin: 0;
        }
        p{
            display: inline-block;
            font-size: 23px;
            font-weight: 500;
            line-height: 2;
            letter-spacing: 0.3px;
            text-align: left;
            margin: 0.5em;
        }
        span {
            color: #06086e;
            background-color: #f8e81c;
            box-shadow: 0.5em 0 0 #f8e81c,-0.5em 0 0 #f8e81c;
        }
    }
    .what-gift{
        position: relative;
        margin-top: 133px;
    }
    h2{
        display: inline-block;
        position: relative;
        font-size: 40px;
        font-weight: bold;
        line-height: 1;
        letter-spacing: 3.9px;
        margin: 0 26px 15px 0;
        color: #06086e;
    }

    h2 div{
        position: relative;
        padding: 6px 16px;
        z-index: 1;
        background: #f8e81c;
    }

    h2:after{
        content: " ";
        position: absolute;
        top: 26px;
        left: 26px;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
    }

    h2 div:before, h2 div:after{
        content: " ";
        position: absolute;
        width: 0;
        height: 0;
    }

    h2 div:before{
        bottom: -27px;
        left: 27px;
        border-top: 19px solid transparent;
        border-bottom: 19px solid transparent;
        border-left:19px solid #c40f0f;

        transform: rotate(-45deg);
        transform-origin: 0 100%;
    }

    h2 div:after{
        right: 0;
        top: 0;
        border-top: 19px solid transparent;
        border-bottom: 19px solid transparent;
        border-right: 19px solid #3230e4;
        transform: rotate(-45deg);
        transform-origin: 100% 0;
    }

    .buttons-conteneur{
        padding-top: 30px;
        padding-bottom: 30px;
        text-align: right;
    }

    button{
        display: inline-block;
        position: relative;
        color: #06086e;
        font-size: 40px;
        padding: 0;
        border: none;
        background-color: transparent;
        margin-right: 26px;

        span{
            font-family: 'Roboto Mono', monospace;
            position: relative;
            display: block;
            text-align: right;
            padding: 5px 20px 5px 150px;
            z-index: 1;
            background: #f8e81c;
            font-weight: bold;
            letter-spacing: 2px;

            &:before, &:after{
                content: " ";
                position: absolute;
                width: 0;
                height: 0;
            }

            &:before{
                bottom: -27px;
                left: 27px;
                border-top: 19px solid transparent;
                border-bottom: 19px solid transparent;
                border-left:19px solid #c40f0f;

                transform: rotate(-45deg);
                transform-origin: 0 100%;
            }

            &:after{
                right: 0;
                top: 0;
                border-top: 19px solid transparent;
                border-bottom: 19px solid transparent;
                border-right: 19px solid #3230e4;

                transform: rotate(-45deg);
                transform-origin: 100% 0;
            }
        }

        &:after{
            content: " ";
            position: absolute;
            top: 26px;
            left: 26px;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
        }
    }


    .conteneur{
        position: relative;
        overflow: hidden;
        transition: all 0.5s ease;
    }

    .result{
        position: absolute;
        padding: 120px 20px;
        transition: all 0.5s 0.25s ease;
        transform: translate(0, 100vh);
        background-color: #06b586;
        background-image: linear-gradient(180deg,#12346d,#06b586);
    }

    &.show-result{
        .conteneur{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            transform: scale(0.8);
            z-index: 0;
        }

        .result{
            position: relative;
            transform: translate(0, 0vh);
            z-index: 1000;
        }
    }
}

.what-gift-title{
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
    z-index: 2;
}

.what-gift-form{
    position: relative;
    display: block;

    .cases{
        position: relative;
        z-index: 1;

        display: inline-flex;
        flex-wrap: wrap;
    }

    .case{
        width: 221.9px;
        //height: 190.1px;
        position: relative;
        margin: 10px;
        cursor: pointer;

        p{
            display: inline-block;
            position: relative;
            font-size: 18px;
            font-weight: bold;
            line-height: 1.67;
            width: 221.9px;
            //height: 190.1px;
            margin: 0;
            text-transform: uppercase;

            letter-spacing: 1.8px;
            text-align: left;
            color: #ffffff;
        }

        p span{
            display: block;
            position: relative;
            padding: 10px 20px;
            z-index: 1;
            width: 221.9px;
            //height: 190.1px;

            background: #12346d;
        }

        p:after{
            content: " ";
            position: absolute;
            top: 10px;
            left: 10px;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to bottom, #12346d, #06b586);
        }

        p span:before, p span:after{
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
        }

        p span:before{
            bottom: -10px;
            left: 10px;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-left: 7px solid #06b586;

            transform: rotate(-45deg);
            transform-origin: 0 100%;
        }

        p span:after{
            right: 0;
            top: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid #12346d;

            transform: rotate(-45deg);
            transform-origin: 100% 0;
        }

        .check{
            //position: absolute;
            height: 43px;
            width: 43px;
            margin-top: 10px;
            margin-bottom: 10px;
            z-index: 1;
            cursor: pointer;
            left: 20px;
            border: solid 9px #fff;
        }

        &:hover, &.active{
            .check{
                background-color: #fff;
            }
        }

        &.active{
            p span{
                background-color: #c40f0f;
            }

            p:after{
                background: #fff;
            }

            p span:before, p span:after{
                content: " ";
                position: absolute;
                width: 0;
                height: 0;
            }

            p span:before{
                border-left-color: #fff;
            }

            p span:after{
                border-right-color: #fff;
            }
        }
    }
    .suivant{
        position: absolute;
        bottom: 40px;
        left: -100px;
        p {
            display: inline-block;
            position: relative;
            font-size: 40px;
            width: 1175px;
            height: 189px;
            font-weight: bold;
            line-height: 1;
            letter-spacing: 3.9px;
            color: #06086e;
        }

        p span{
            position: relative;
            display: block;
            text-align: right;
            padding-top: 130px;
            padding-right: 15px;
            z-index: 1;
            width: 1175px;
            height: 189px;
            background: #f8e81c;
        }

        p:after{
            content: " ";
            position: absolute;
            top: 26px;
            left: 26px;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(90deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
        }

        p span:before, p span:after{
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
        }

        p  span:before{
            bottom: -27px;
            left: 27px;
            border-top: 19px solid transparent;
            border-bottom: 19px solid transparent;
            border-left:19px solid #c40f0f;

            transform: rotate(-45deg);
            transform-origin: 0 100%;
        }

        p span:after{
            right: 0;
            top: 0;
            border-top: 19px solid transparent;
            border-bottom: 19px solid transparent;
            border-right: 19px solid #3230e4;

            transform: rotate(-45deg);
            transform-origin: 100% 0;
        }
    }

}

/* FINAL */


.final-left, .final-right{

    position: relative;
    max-width: 1260px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 50px auto 120px auto;
    margin-right: auto;
    overflow: hidden;

    a, button{
        font-style: normal;
        display: inline-block;
        position: relative;
        color: #06086e;
        font-size: 18px;
        font-weight: bold;
        padding: 0;
        border: none;
        background-color: transparent;
        margin: 0px 15px;

        span{
            position: relative;
            display: block;
            text-align: right;
            padding: 15px 35px;
            z-index: 1;
            background: #f8e81c;

            &:before, &:after{
                content: " ";
                position: absolute;
                width: 0;
                height: 0;
            }

            &:before{
                bottom: -10px;
                left: 10px;
                border-top: 7px solid transparent;
                border-bottom: 7px solid transparent;
                border-left:7px solid #3230e4;

                transform: rotate(-45deg);
                transform-origin: 0 100%;
            }

            &:after{
                right: 0;
                top: 0;
                border-top: 7px solid transparent;
                border-bottom: 7px solid transparent;
                border-right: 7px solid #c40f0f;

                transform: rotate(-45deg);
                transform-origin: 100% 0;
            }
        }

        &:after{
            content: " ";
            position: absolute;
            top: 10px;
            left: 10px;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to bottom, #c40f0f, #3230e4);;
        }
    }


    h2{
        display: inline-block;
        position: relative;
        font-size: 40px;
        font-weight: bold;
        line-height: 1;
        letter-spacing: 3.9px;
        color: #fff;
        margin-bottom: 70px;

        div{
            position: relative;
            padding: 10px 16px 10px 70px;
            z-index: 2;
            background: #13346d;
        }

        .number{
            position: absolute;
            display: block;
            padding: 10px 16px;
            letter-spacing: 0;
            top: 0;
            left: 0;
        }
    }

    h2:after{
        content: " ";
        position: absolute;
        top: 26px;
        left: 26px;
        width: 100%;
        z-index: 1;
        height: 100%;
        background-image: linear-gradient(102deg, #c40f0f, #af142e 15%, #a5163c 21%, #3230e4);
    }

    h2 div:before, h2 div:after{
        content: " ";
        position: absolute;
        width: 0;
        height: 0;
    }

    h2 div:before{
        bottom: -27px;
        left: 27px;
        border-top: 19px solid transparent;
        border-bottom: 19px solid transparent;
        border-left:19px solid #c40f0f;

        transform: rotate(-45deg);
        transform-origin: 0 100%;
    }

    h2 div:after{
        right: 0;
        top: 0;
        border-top: 19px solid transparent;
        border-bottom: 19px solid transparent;
        border-right: 19px solid #3230e4;

        transform: rotate(-45deg);
        transform-origin: 100% 0;
    }

    .text-result{
        position: relative;
        background-color: #fff;
        display: inline-block;
        width: 60%;
        padding: 50px;
        padding-top: 100px;
        margin-top: -100px;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.67;
        letter-spacing: 1px;
        text-align: left;
        margin-bottom: 20px;
        color: #13346d;
    }

    .img-console-result{
        position: relative;
        max-width: 50%;
        display: inline-block;
        z-index: 1;
        margin-bottom: 26px;
        margin-left: 15%;

        img{
            display: block;
            width: 100%;
            position: relative;
            border: solid 2px #f8e81c;
            z-index: 1;
        }

        &:before, &:after, div:before, div:after{
            content: " ";
            display: block;
            position: absolute;
        }

        div{
            &:before{
                width: 27px;
                height: calc(~"100% - 27px");
                bottom: 0;
                right: -27px;
                background-image: linear-gradient(to bottom, #12346d, #06b586)
            }

            &:after{
                bottom: -27px;
                left: 27px;
                border-top: 19px solid transparent;
                border-bottom: 19px solid transparent;
                border-left:19px solid #06b586;
                transform: rotate(-45deg);
                transform-origin: 0 100%;
            }
        }

        &:before{
            width: 100%;
            left: 27px;
            height: 27px;
            bottom: -27px;
            background-color: #06b586;
        }

        &:after{
            top: 0;
            right: 0;
            border-top: 19px solid transparent;
            border-bottom: 19px solid transparent;
            border-right:19px solid #12346d;
            transform: rotate(-45deg);
            transform-origin: 100% 0;
        }
    }

    .prix{
        display: inline-block;
        float: right;
        color: #3230e4;
    }

    h3{
        margin-bottom: 5px;
    }

    h4{
        text-align: right;
        font-size: 16px;
        margin: 0;
    }
}


.final-left{
    .img-console-result{
        float: left;
    }
    .text-result{
        float: right;
    }
    .buttons{
        position: absolute;
        left: 0;

        button:first-child, a:first-child{
            margin-left: -35px;
        }
    }

}
.final-right{
    .img-console-result{
        margin-left: 0;
        margin-right: 15%;
    }
    div{
        text-align: right;
        margin-right: 0;
    }
    .h2{
        text-align: right;
        margin-right: 61px;
    }
    .img-console-result{
        float: right;
    }
    .text-result{
        float: left;
        margin-right: 0
    }
    .buttons{
        position: absolute;
        right: 0;


        &:last-child{
            margin-right: -35px;
        }
    }
}

.who-footer{
    position: relative;
    padding: 120px 10px;
    text-align: center;
}

.table{ display: table; }
.row{ display: table-row; }
.cell{ display: table-cell; vertical-align: middle; }

.centerer{
    display: table;
    width: 100%;
    height: 100%;
}

.centered{
    display: table-cell;
    vertical-align: middle;
}

.pxl-center{
    position: relative;
    margin: 0 auto;
    max-width: 1000px;
    padding-left: 15px;
    padding-right: 15px;
}

.text-center{
    text-align: center;
}

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    *zoom: 1;
}

@media screen and (max-width: 1600px){
    .consoles .img-console{
        max-width: 400px;

        img{
            max-height: 400px;
        }
    }
}

@media screen and (max-width: 1400px){
    .consoles .img-console{
