@media screen and (max-width: 768px){
    .pxl-grand-format{
        h2{
            font-size: 24px;
            letter-spacing: 0;
            margin-right: 0;
            margin-bottom: 10px;
            div{
                padding: 6px 10px;
                &:before, &:after{
                    border-top-width: 12px;
                    border-bottom-width: 12px;
                }
                &:before{
                    bottom: -17px;
                    left: 17px;
                    border-left-width: 12px;
                }
                &:after{
                    border-right-width: 12px;
                }
            }
            .number{
                padding: 6px 10px;
            }
            &:after{
                top: 16px;
                left: 16px;
            }
        }
        button{
            font-size: 20px;
            span{
                padding: 5px 20px 5px 50px;
            }
        }
        .title-top{
            margin-top: 100px;
            h1{
                font-size: 36px;
                letter-spacing: 3px;
            }
            p{
                line-height: 1.8;
                font-size: 16px;
            }
        }
        .pxl-center{
            padding-left: 10px;
            padding-right: 10px;
        }
        .what-gift{
            margin-top: 60px;
            margin-bottom: 120px;
        }
    }

    .what-gift-form{
        margin-top: 60px;
        .cases{
            margin-left: 5px;
            margin-right: 5px;
            justify-content: center;
        }
        .case{
            width: 135px;
            margin: 5px;

            p{
                font-size: 14px;
                width: 135px;
                margin: 0;
                letter-spacing: 0;
                span{
                    padding: 5px 10px;
                    width: 135px;
                    &:before, &:after{
                        border-top-width: 4px;
                        border-bottom-width: 4px;
                    }
                    &:before{
                        bottom: -6px;
                        left: 6px;
                        border-left-width: 4px;
                    }
                    &:after{
                        border-right-width: 4px;
                    }
                }
                &:after{
                    top: 6px;
                    left: 6px;
                }
            }

            .check{
                height: 30px;
                width: 30px;
                margin-bottom: 6px;
                border-width: 6px;
            }
        }
        .suivant{
            right: 0;
            left: 0;
            p{
                padding: 0;
                margin: 0;
                width: 100%;
                height: 160px;
                span{
                    padding: 0;
                    margin: 0;
                    width: 100%;
                    height: 160px;
                    &:before, &:after{
                        border-top-width: 12px;
                        border-bottom-width: 12px;
                    }
                    &:before{
                        bottom: -17px;
                        left: 17px;
                        border-left-width: 12px;
                    }
                    &:after{
                        border-right-width: 12px;
                    }
                }
                &:after{
                    top: 16px;
                    left: 16px;
                }
            }
        }
    }

    .final-left, .final-right{
        margin: 60px auto 20px;
        h2 div{
            padding: 6px 10px 6px 45px;
        }
        &:first-child{
            margin-top: 20px;
        }
        .text-result{
            width: 100%;
        }
        .img-console-result{
            max-width: 75%;
            margin-top: 30px;
            margin-bottom: 30px;
            margin-left: 10%;
            img{
                margin-left: 0;
            }
        }
        .buttons{
            left: 0;
            right: 0;
            text-align: center;
            &:last-child{
                margin-right: 0;
            }
            a, button{
                margin: 0 5px;
                span{
                    padding: 15px;
                }
                &:first-child{
                    margin-left: 0;
                }
            }
        }
    }
}

